const renderQuote = ({ text, author, url }) => {
  return (
    <blockquote cite={url} key={author}>
      <p>
        {/*  */}
        <i className="icon-quote-left" />
        {text}
        <i className="icon-quote-right" />
      </p>
      <p className="author">
        &mdash; <a href={url}>{author}</a>
      </p>
    </blockquote>
  );
};

